import resultService from './../services/results'

// This can be implemented if found reasonable
//JSON.parse(localStorage.getItem('results'))
//localStorage.setItem('results', JSON.stringify(results))

const resultReducer = (state = [], action) => {
  switch(action.type) {
    case 'COMPUTE_RESULTS':
      return action.data
    case 'RESET_RESULTS':
        return action.data
    default:
      return state
  }
}

export const computeResults = (answers) => {
  return async (dispatch) => {
    const results = answers.length > 0 ? await resultService.showResults(answers) : []
  dispatch({
      type: 'COMPUTE_RESULTS',
      data: results
    })
  }
}

export const resetResults = () => {
  return async (dispatch) => {
    const results = []
  dispatch({
      type: 'RESET_RESULTS',
      data: results
    })
  }
}
export default resultReducer



