import React from 'react'
import {Table} from 'react-bootstrap'
import {connect} from 'react-redux'
import {computeResults} from '../reducers/resultReducer'
//import AccuracyRate from './AccuracyRate'

class ResultList extends React.Component {
  componentWillMount = async () => {
    await this.props.computeResults(this.props.answers)
  }

  render(){
      if(this.props.results === undefined | this.props.results.length === 0) {
        return <div></div>
      }
      const results = this.props.results
      return (
        <div className="results">
          <Header language={this.props.language}/>
          <Table>
            <tbody>
            {results.map(a =>
              <tr key = {a.model}>
                <td>
                  <a href={a.link}>
                    <img src={a.photo} alt={a.model} style={{width: 250, marginRight:5}} />
                    <br/>
                    {a.model}
                  </a>
                  <br/>
                  {MatchLevel(this.props.language, a.score)}
                </td>
              </tr>
            )}
            </tbody>
          </Table>
        </div>
      )
  }
}

const MatchLevel = (language, score) => {
  const evaluation_values = language === "EN" ? ["Perfect match!", "Very good option", "Considerable option", "Probably not what you're looking for"] : ["Täydellinen valinta!", "Todella hyvä vaihtoehto", "Harkitse myös tätä", "Tuskin etsit tätä mallia"]
  let idx = 0
  if (score > 88) {
    idx = 0
  } else if (score > 80) {
    idx = 1
  } else if (score > 69) {
    idx = 2
  } else {
    idx = 3
  }
  return(
    <div className="results-match-box"><p>{evaluation_values[idx]}</p></div>
  )
}
const Header = (props) => {
  const header = props.language === "EN" ? "Results" : "Tulokset"
  const body = props.language === "EN" ? "Here are the recommended models listed from most suitable to least matching." : "Allaolevat mallit on järjestetty vastaustesi perusteella sopivimmasta vaihtoehdosta alkaen."
  return(
    <div className="result-info">
      <h2 style={{marginBottom:20}}>{header}</h2>
      <h5 style={{marginBottom:20}}>{body}</h5>
    </div>
  )
}

const mapStateToProps = (state) => {
    return {
      questions: state.questions,
      answers: state.answers,
      results: state.results,
      language: state.language
    }
}


export default connect(
  mapStateToProps,{computeResults}
)(ResultList)



